/* Utility: UI animation
============================================================================ */
@keyframes fadeOut {
  0% {
    display: block;
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-100%); }
  100% {
    transform: translateY(0);
    opacity: 1; } }

.u-zoom-in {
  animation-name: zoomIn; }

.pulse {
  animation-name: pulse; }

.animated {
  animation-duration: 1s;
  animation-fill-mode: both; }

.animated.infinite {
  animation-iteration-count: infinite; }

.animated.hinge {
  animation-duration: 2s; }

/* Underline From Center */
.underline-from-center, .siteNav_item {
  display: inline-block;
  vertical-align: middle;
  transform: translateZ(0);
  will-change: transform;
  box-shadow: 0 0 1px transparent;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  overflow: hidden;
  vertical-align: bottom; }
  .underline-from-center:before, .siteNav_item:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 50%;
    right: 50%;
    bottom: 0;
    background: #2098D1;
    height: 2px;
    transition-property: "left, right";
    transition-duration: 0.3s;
    transition-timing-function: ease-out; }
  .underline-from-center:hover:before, .siteNav_item:hover:before, .underline-from-center:focus:before, .siteNav_item:focus:before, .underline-from-center:active:before, .siteNav_item:active:before {
    left: 0;
    right: 0; }

/* Underline From Left */
@keyframes pulse {
  from {
    transform: scale3d(1, 1, 1); }
  50% {
    transform: scale3d(1.2, 1.2, 1.2); }
  to {
    transform: scale3d(1, 1, 1); } }

@keyframes bounce {
  from {
    transform: scale3d(1, 1, 1); }
  50% {
    transform: scale3d(1, 1.1, 1); }
  to {
    transform: scale3d(1, 1, 1); } }

@keyframes scale-up {
  from {
    transform: scale3d(1, 1, 1); }
  to {
    transform: scale3d(1.2, 1.2, 1.2); } }

